<template>
    <div>
        <socket-window v-if="inited"></socket-window>
        <router-view v-if="inited"></router-view>
    </div>
</template>

<script>
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex';

    import SocketWindow from '@/components/apply/socketWindow';

    export default {
        name: 'orderIndex',
        components: {
            SocketWindow
        },
        data() {
            return {
                inited: false,
            }
        },
        computed: {
            ...mapState('app', [
                'loading',
                'zfLoading'
            ]),
        },
        created() {
        },
        mounted() {
            this.initLocalDict();//初始化本地字典
            this.inited = true;
        },
        //离开当前页面后执行
        destroyed() {
            this.Socklink.disconnect();
        },
        methods: {
            ...mapMutations('app', [
                'showLoading',
                'hideLoading',
                'rejectEdit'
            ]),
            ...mapMutations('dict', ['initLocalDict'])
        }
    }
</script>



